.header {
  display: flex;
  justify-content: space-between;
}

@mixin darkTheme( $light,$dark) {
  @each $key, $val in $light {
    #{$key}: #{$val}
  }
  @media (prefers-color-scheme: dark) {
    @each $key, $val in $dark {
      #{$key}: #{$val}
    }
  }
  &.LightTheme,
  .LightTheme & {
    @each $key, $val in $light {
      #{$key}: #{$val}
    }
  }
  &.DarkTheme,
  .DarkTheme & {
    @each $key, $val in $dark {
      #{$key}: #{$val}
    }
  }
}

body {
  @include darkTheme((
          --text-color: black,
          --background-color: white,
  ), (
          --text-color: white,
          --background-color: black,
  ))
}

body {
  color: var(--text-color);
  background: var(--background-color);
}
